@import './vars';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    overflow-y: scroll;
    overflow-x: hidden;
    scroll-behavior: smooth;
    max-width: 100vw;
}

body {
    font-family: 'Raleway', sans-serif;
    scroll-behavior: smooth;
    max-width: 100vw;

}
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-thumb {
    background: #498486;
}

h1,h2,h3,h4,h5,h6,p {
    pointer-events: none;
    color: #000;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3rem;
}

h3 {
    font-size: 2.5rem;
}


h4 {
    font-size: 2rem;
}

h5 {
    font-size: 1.5rem;
}

h6 {
    font-size: 1.2rem;
}

p {
    font-size: 1.3rem;
    font-weight: 300;
    line-height: 28px;
    font-family: calibri;
}

button {
    font-family: 'Raleway', sans-serif;
}

svg {
    fill: white;
    height: 40px;
    width: auto;
    transition: all 0.2s ease-out;
}

.app {
    width: 100%;
}


.container {
    width: 80%;
    max-width : $xLargeScreen;
    margin: auto;
    height : 100%;
}

.section{
    width: 100vw;
    padding : 80px 0;
    min-height : 100vh;

    
}

