.headline {
    margin-bottom: 80px;
    display: flex;
    flex-direction: row;
    z-index: 2 !important;

    .left-line{
        width : 6px;
        margin-right : 10px;
        background: $accent-color ;
        z-index: 2 !important;

    }

    .headline-typo{
        z-index: 2 !important;
        h5{
            font-weight : 700;
            letter-spacing: 2px;

        }
    }
}

@media screen and (max-width: $xSmallScreen) {

    .container{
        width : 85%;
    }
}


@media screen and (max-width: $smallScreen) {
    ::-webkit-scrollbar {
        width: 0;
    }

}

